.offers {
  position: relative;
  background-color: $blue;
  font-family: $roboto;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  color: $white;
  padding-top: 90px;
  padding-bottom: 40px;

  @media (min-width: $medium) {
    background-color: inherit;
    padding-top: 0;
    padding-bottom: 0;
  }

  @media (min-width: $large) {
    flex-basis: 50%;
  }
}

.offers__layout {
  padding: 0 25px;

  @media (min-width: $medium) {
    background-color: $blue;
    padding: 133px 0 73px;
  }

  @media (min-width: $large) {
    padding-top: 244px;
    height: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.offers__full {
  margin-bottom: 10px;

  @media (min-width: $medium) {
    margin-bottom: 27px;
  }

  @media (min-width: $large) {
    margin-bottom: 10px;
  }
}

.offers__full__text {
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  letter-spacing: -0.5px;
  color: rgba($white, 0.4);
  margin-top: 0;
  margin-bottom: 7px;
  display: block;
}

.offers__full-price {
  font-family: $rs;
  font-weight: 300;
  font-size: 36px;
  line-height: 47px;
  color: rgba($white, 0.4);
  letter-spacing: -2px;
  display: inline-block;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;

  &::after {
    @include pseudo();
    @include size(100%, 2px);
    background-color: $pastel-purple;
    top: 50%;
    transform: translateY(-50%);
  }

  @media (min-width: $large) {
    svg {
      @include size(21px, 27px)
    }
  }
}

.offers__installment {
  margin-bottom: 27px;

  @media (min-width: $medium) {
    margin-bottom: 35px;
  }

  @media (min-width: $large) {
    margin-bottom: 31px;
  }
}

.offers__installment-text {
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: -0.5px;
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: $medium) {
    font-size: 20px;
  }

  @media (min-width: $large) {
    font-size: 23px;
    line-height: 27px;
  }
}

.offers__installment-price {
  font-weight: normal;
  font-family: $rs;
  font-size: 56px;
  line-height: 1.3;
  letter-spacing: -2px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: -3px;
  margin-top: 0;

  span {
    margin-right: -4px;
  }

  @media (min-width: $medium) {
    font-size: 80px;
    line-height: 1.3;
    margin-bottom: -10px;

    span {
      margin-right: 0;
    }

    svg {
      @include size(50px, 100px);
    }
  }

  @media (min-width: $large) {
    padding-left: 12px;
  }
}

.offers__first-text {
  font-weight: normal;
  font-size: 15px;
  line-height: 1.2;
  letter-spacing: -0.5px;
  margin-top: 0;
  margin-bottom: 0;

  &--s {
    display: none;
  }

  @media (min-width: $medium) {
    font-size: 18px;
    line-height: 1.16;
  }

  @media (min-width: $large) {
    font-size: 20px;
    line-height: 1.15;

    &--s {
      display: block;
    }
    &--f {
      display: none;
    }
  }
}

.offers__first-price {
  font-family: $rs;
  font-weight: 300;
  font-size: 40px;
  line-height: 53px;
  letter-spacing: -2px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;

  span {
    margin-right: 10px;
  }

  @media (min-width: $medium) {
    font-size: 50px;
    line-height: 66px;

    span {
      margin-right: 0;
    }

    svg {
      @include size(23px, 34px)
    }
  }

  @media (min-width: $large) {
    margin-bottom: 3px;
  }
}

.offers__first__svg {
  @include size(19px, 29px);
}
